




























export default {
	data() {
		return {
			name:'',
			map_address:'',
			link_name:'',
			link_tel:'',
			src:'',
			id:'',	//照片id
		};
	},
	methods:{
		getInfo(e,str){
			var that = this;
			if(str=='name'){
				that.name = e.detail.value
			}else if(str == 'address'){
				that.map_address = e.detail.value
			}else if(str == 'link_name'){
				that.link_name == e.detail.value
			}else if(str == 'link_tel'){
				that.link_tel == e.detail.value
			}
		},
		// 提交信息
		submit(){
			var that = this;
			
		},
		// 上传图片
		uploadImg(){
			var that = this;
			// 选择图片
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album'],
				success: (wj) => {
					uni.showLoading({
						title: '上传中...'
					})
					// 原生压缩
					plus.zip.compressImage({
							src: wj.tempFilePaths[0],
							dst: "_doc/a.jpg",
							overwrite: true,
							width: '270px',
							height: '270px',
							format: 'jpg',
							quality: 100
						},
						function(e) {
							var reader = new plus.io.FileReader();
							reader.onloadend = function(e) {
								var speech = e.target.result; //base64图片 
								// 上传头像
								uni.request({
									url: that.http + 'index/imgupload',
									header: that.header,
									method: 'POST',
									data: {
										file: speech
									},
									success: (res) => {
										uni.hideLoading()
										if(res.data.code==200){
											that.src = res.data.path;
											that.id = res.data.id
										}else{
											uni.showToast({
												title:res.data.info,
												icon:'none'
											})
										}
									},
									fail() {
										uni.showToast({
											title: '上传时网络延迟，请刷新后重试!',
											icon: 'none'
										})
									}
								})
							};
							reader.readAsDataURL(e.target);
						},
						function(err) {
							plus.nativeUI.alert('未知错误！', function() {});
						}
					);
				},
				fail() {
					uni.showToast({
						title: '打开相册失败，请重试！',
						icon: 'none'
					})
				}
			})
		}
	}
}
